<!-- 
progectgroups


https://gitlab.com/datsteam/project/dev-bro/-/blob/DEVBRO-14/api/src/main/java/com/devbro/api/dto/ProjectGroupDto.java

public class ProjectGroupDto {
    @JsonProperty("id")
    private String id;

    @JsonProperty("name")
    private String name;
}



    1. id
    2.    name
    3.    workspace

          //        : 
        projectgroups: [
        
                ]




-->


<template>
    <div class="root">
        <v-card text>
                     <v-toolbar text prominent class="no-padding-toolbar">
                <!--   "", "",
                "" -->         
                <v-toolbar-items>
                    <v-tooltip bottom>
                       <template v-slot:activator="{ on }"  >
                       <v-btn v-on="on" @click="dialog = true,edit=false">
                            <v-icon medium>add</v-icon>
                        </v-btn>
                        </template>
                        <span>    </span>
                    </v-tooltip>
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on }"  >
                          <v-btn v-on="on" @click="editItem">
                            <v-icon medium>edit</v-icon>
                          </v-btn>
                        </template>
                        <span></span>
                    </v-tooltip>
                    <v-tooltip bottom>
                       <template v-slot:activator="{ on }"> 
                        <v-btn v-on="on"  @click="delItem" >
                           <v-icon medium>delete</v-icon>
                        </v-btn>
                        </template>
                        <span>  </span>
                    </v-tooltip>
                </v-toolbar-items>
                <!--   -->
                <v-text-field
                        v-model="search"
                        append-icon="search"
                        :label="search"
                        single-line
                        hide-details>
                </v-text-field>
            </v-toolbar>
            <v-divider>
            </v-divider>
           
           <!--  :tran1=""-->
           <!--
           
            

            https://vuetifyjs.com/en/components/data-tables/             


            multi-sort-     
                  -

            show-select-   
            :single-select="singleSelect" -   
             
             data:  
            singleSelect:true,

            v-model="search"   label="Search"  
            :headers="commonHeaders" -     commonHeaders

            :items="projectGroups" -     projectGroups

            class="fixed-headers" -  

            :items-per-page="5" -      
           -->   
            
            <!--     (commonHeaders)
               (  projectGroups  )-->
            <v-data-table
                    :headers="commonHeaders"
                    :items="projectGroups"
                    :search="search"
                    v-model="selected"
                    item-key="id"
                    :no-data-text="' '"
                    :no-results-text="'  '"
                    show-select
                    :single-select="singleSelect"
                    class="fixed-headers"
                    :items-per-page="5"
                    >

            </v-data-table>
        </v-card>


        <!--
                              Dilog    
             "  "
         " " -->
        <v-dialog v-model="dialog" persistent max-width="600px">
            <v-card>
                <v-form ref="form">
                    <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap>                    
                               <v-flex xs12>
                                    <v-text-field :label="''" v-model="name"
                                                  required></v-text-field>
                                </v-flex>
                                <v-flex xs12>                                               
                               <template>
                                 <p>  : </p>                               
                               </template>
                               
                             <v-combobox
                                   v-model="selectedWorkspace"
                                   :items="workspaces"
                                   item-text="name"
                                   item-value="name"
                                   label=" "
                                   dense
                                   filled
                                   outlined
                                   solo
                                ></v-combobox> 
          
                                </v-flex>

                            </v-layout>
                        </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close" closeMethod>{{''}}</v-btn>
                        <v-btn color="blue darken-1" text @click="addEditItem">{{''}}</v-btn>
                    </v-card-actions>
                </v-form>
            </v-card>
        </v-dialog>
        <!-- ********************************************************  -->
    </div>
</template>

<script>

export default {
      
    data() {
        return {                
                id:'',
                name:'',
                workspae:{id:"",name:"",active:false,logo:""},                   
                dialog: false,
                edit:false,
                selected: [],
                search: '',
                singleSelect:true,
                commonHeaders: [
                    {text: 'id', align: 'left', value: 'id'},
                    {text: '', align: 'left', value: 'name'},
                    {text: ' ', align: 'left', value: 'workspace.name'}                    
                ],
                selectedWorkspace: {id:"",
                name:" ", 
                active:"",logo:""}


            }
        },
 methods: {
            //      " "
            //     Dilog
            // ( )
               editItem() {
                  if (this.selected.length === 0) {
                    alert('     ')
                } else {
                console.log(this.selected[0]);
                //    
                 this.id=this.selected[0].id;
                 this.name=this.selected[0].name;                 
                 //  :             
                 // this.workspace = this.selected[0].workspace
                 //   ,  :
                 let a =   this.selected[0] 
                 let b={}                  
                 b.id =      a.workspace.id
                 b.name =    a.workspace.name 
                 b.active =  a.workspace.active
                 b.logo =    a.workspace.logo           
                 
                this.workspace = b 

                // console.log('a.workspace.id',a.workspace.id)  
                // console.log('b.id',b.id) 
                // console.log('this.workspace ',this.workspace ) 
                 
                //  -    
                //      
                // (       ,
                //    )
                //  :
                // this.selectedWorkspace =  this.selected[0].workspae
                //   ,  
                this.selectedWorkspace = b;
                console.log('this.selectedWorkspace ',this.selectedWorkspace ) 

                 this.edit=true
                 this.dialog = true                 
                }
            },   
            //     ""
            //    "Dilog"
            addEditItem() {
                //   
                this.dialog = false;
                //       
                //      id    
                if (this.edit === false) {
                let form = {
                    id: this.id,
                    name: this.name,
                    workspae: this.selectedWorkspace
                };

                    this.$store.dispatch('addProjectGroup', form);
                    this.$refs.form.reset()
                                      }
                //      
                //    
                 if (this.edit === true) {


                let form = {
                    id: this.id,
                    name: this.name,
                    workspae: this.selectedWorkspace
                };

                    const msg = '    ?';
                    let boo = confirm(msg) && this.$store.dispatch('editProjectGroup', form);
                    if (boo === true) {
                        this.selected = [];
                    } else {
                        this.selected = [];
                    }

                    
                    this.$refs.form.reset()
                                      }
            },
          //      
          //   Dilog
            close() {
                this.dialog = !this.dialog;
                this.$refs.form.reset();
                this.isEdit = false;
            },

             //   
            delItem() {
                if (this.selected.length === 0) {
                    alert('     ')
                } else {
                    const msg = '  ?';
                    let boo = confirm(msg) && this.$store.dispatch('deleteProjectGroup', this.selected[0])
                    if (boo === true) {
                        this.selected = [];
                    } else {
                        this.selected = [];
                    }
                }
            },
    
            setData() {
           //     this.$store.dispatch('projectGroups')
            }
        },


        created() {
           //  this.setData();
        },

        watch: {
       
        },

        computed: {           
        //     projectGroups  store/projectGroups
            projectGroups() {
                    return this.$store.getters.projectGroups
            },
        //      workspaces
            workspaces() {
              return this.$store.getters.workspaces
            }
        }
    }

</script>

<style scoped>
   .root{
        font-size:13px!important;
   }

</style>