//   :
//      vue:
// loading-  
// error - 

export default {
  state: {
    loading: false,
    error: null
  },
  mutations: {
    // 
    setLoading (state, payload) {
      state.loading = payload
    },
    // 
    setError (state, payload) {
      state.error = payload
    },
    //  
    clearError (state) {
      state.error = null
    }
  },
  actions: {
    //   commit
    setLoading ({commit}, payload) {
      //  commit  mutation setLoading
      commit('setLoading', payload)
    },
    
    setError ({commit}, payload) {
      commit('setError', payload)
    },
    clearError ({commit}) {
      commit('clearError')
    }
  },
  getters: {
    loading (state) {
      return state.loading
    },
    error (state) {
      return state.error
    }
  }
}
