<template>
    <div class="root">
        <v-card text>
                     <v-toolbar text prominent class="no-padding-toolbar">
                <!--   "", "",
                "" -->         
                <v-toolbar-items>
                    <v-tooltip bottom>
                       <template v-slot:activator="{ on }"  >
                       <v-btn v-on="on" @click="dialog = true,edit=false">
                            <v-icon medium>add</v-icon>
                        </v-btn>
                        </template>
                        <span>    </span>
                    </v-tooltip>
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on }"  >
                          <v-btn v-on="on" @click="editItem">
                            <v-icon medium>edit</v-icon>
                          </v-btn>
                        </template>
                        <span></span>
                    </v-tooltip>
                    <v-tooltip bottom>
                       <template v-slot:activator="{ on }"> 
                        <v-btn v-on="on"  @click="delItem" >
                           <v-icon medium>delete</v-icon>
                        </v-btn>
                        </template>
                        <span>  </span>
                    </v-tooltip>
                </v-toolbar-items>
                <!--   -->
                <v-text-field
                        v-model="search"
                        append-icon="search"
                        :label="search"
                        single-line
                        hide-details>
                </v-text-field>
            </v-toolbar>
            <v-divider>
            </v-divider>
           
           <!--  :tran1=""-->
           <!--
           
            

            https://vuetifyjs.com/en/components/data-tables/             


            multi-sort-     
                  -

            show-select-   
            :single-select="singleSelect" -   
             
             data:  
            singleSelect:true,

            v-model="search"   label="Search"  
            :headers="commonHeaders" -     commonHeaders

            :items="workspaces" -     workspaces

            class="fixed-headers" -  

            :items-per-page="5" -      
           -->   
            
            <!--     (commonHeaders)
               (  workspaces  )-->
   <v-data-table
                    :headers="commonHeaders"
                    :items="workspaces"
                    :search="search"
                    v-model="selected"
                    item-key="id"
                    :no-data-text="' '"
                    :no-results-text="'  '"
                    show-select
                    :single-select="singleSelect"
                    :items-per-page="5"
                    >

        <template v-slot:[`item.logo`]="{ item }">
            <v-chip>
            <span> <a :href="item.logo"> 
            <img width="30" height="30" :src="item.logo"> 
            </a></span>
           </v-chip>
        </template>
    
         <template v-slot:[`item.active`]="{ item }">
         <v-checkbox
            v-model="item.active"
            disabled
         ></v-checkbox>
        </template>
 
            </v-data-table>
        </v-card>


        <!--
                              Dilog    
             "  "
         " " -->
        <v-dialog v-model="dialog" persistent max-width="600px">
            <v-card>
                <v-form ref="form">
                    <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap>            
                               <v-flex xs12>
                                    <v-text-field :label="''" v-model="name"
                                                  required></v-text-field>
                                </v-flex>
                                <v-flex xs12>
                            <v-checkbox
                             v-model="active"
                             label="">
                            
                            </v-checkbox>
                      
                                </v-flex>
                                <v-flex xs12>
                                    <v-text-field :label="''" v-model="logo"
                                                  required></v-text-field>
                                </v-flex>                                
                            </v-layout>
                        </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn color="blue darken-1" text @click="close" closeMethod>{{''}}</v-btn>
                        <v-btn color="blue darken-1" text @click="addEditItem">{{''}}</v-btn>
                    </v-card-actions>
                </v-form>
            </v-card>
        </v-dialog>
        <!-- ********************************************************  -->
    </div>
</template>

<script>

export default {
      
    data() {
        return {                
                id:'',
                name:'',
                active:false, 
                logo:"",                         
                dialog: false,
                edit:false,
                selected: [],
                search: '',
                singleSelect:true,
                commonHeaders: [
                    {text: 'id', align: 'left', value: 'id'},
                    {text: '', align: 'left', value: 'name'},
                    {text: '', align: 'left', value: 'active'},                    
                    {text: '', align: 'left', value: 'logo'}
                ]
            }
        },
 methods: {

            //      " "
            //     Dilog
            // ( )
               editItem() {
                  if (this.selected.length === 0) {
                    alert('     ')
                } else {
                console.log(this.selected[0]);
                 this.id=this.selected[0].id;
                 this.name=this.selected[0].name;
                 this.active=this.selected[0].active;
                 this.logo=this.selected[0].logo;
                     
                 this.edit=true
                 this.dialog = true                 
                }
            },   
            //     ""
            //    "Dilog"
            addEditItem() {
                //   
                this.dialog = false;
                //       
                //      id    
                if (this.edit === false) {
                let form = {
                    id: this.id,
                    name: this.name,
                    active: this.active,
                    logo: this.logo
                };

                    this.$store.dispatch('addWorkspace', form);
                    this.$refs.form.reset()
                                      }
                //      
                //    
                 if (this.edit === true) {


                let form = {
                    id: this.id,
                    name: this.name,
                    active: this.active,
                    logo: this.logo
                };

                    const msg = '    ?';
                    let boo = confirm(msg) && this.$store.dispatch('editWorkspace', form);
                    if (boo === true) {
                        this.selected = [];
                    } else {
                        this.selected = [];
                    }

                    
                    this.$refs.form.reset()
                                      }
            },
          //      
          //   Dilog
            close() {
                this.dialog = !this.dialog;
                this.$refs.form.reset();
                this.isEdit = false;
            },

             //   
            delItem() {
                if (this.selected.length === 0) {
                    alert('     ')
                } else {
                    const msg = '  ?';
                    let boo = confirm(msg) && this.$store.dispatch('deleteWorkspace', this.selected[0])
                    if (boo === true) {
                        this.selected = [];
                    } else {
                        this.selected = [];
                    }
                }
            },
    
            setData() {
           //     this.$store.dispatch('workspaces')
            }
        },


        created() {
           //  this.setData();
        },

        watch: {
       
        },

        computed: {           
        //     workspaces  store/workspaces
            workspaces() {
                    return this.$store.getters.workspaces
            }
            
        }
    }

</script>

<style scoped>
   .root{
        font-size:13px!important;
   }

