class AddMetric {
  constructor (id, name, description) {
    this.id = id
    this.name = name
    this.description = description 
  }
}



export default {
    state: {  
          //       
        metrics: [
            {
                "id":"1",
                "name":"metric1",
                "description":" "
                  },
            {
                "id":"2",
                "name":"metric2",
                "description":" "
                  },
            {
                "id":"3",
                "name":"metric3",
                "description":" "
             }                      
                ]

        
        },
        mutations: { 
            //  
            deleteMetric (state, {id}) {                          
              //  -          state
              const index = state.metrics.findIndex(a => a.id === id);

              console.log("delete element index",index);
              //    state metrics    1  1 
              state.metrics.splice(index, 1);
              },

             //  
              addMetric (state, {id,name,description}) {   
                
                console.log("add",id,name,description) 
                //         state
                //    AddMetric                
                 state.metrics.push(new AddMetric(id,name,description));
                },
              //  
              editMetric (state, {id, name, description}) {    
                
                console.log("edit",id,name,description) 
                 //     state.metrics
                 // find    .   .
                 //  id -  .
                   const element = state.metrics.find(a => {
                   return a.id === id
               })
                //   
                //  ,   
                element["name"] = name
                element["description"] = description
               }  
              },
           actions: {  
               
            //      state
            // payload-    (  id)
                 deleteMetric({commit},{id}){
                   //  mutation deleteId
                   //      id   
                    commit('deleteMetric',{id})
                 },



            //      state
            addMetric({commit},{name,description}){
           //    id   
             const id = Math.round(Math.random()*1000000000000).toString();

              //  mutation addMetric 
              //        
               commit('addMetric',{id,name,description})
            },

            //      state
            editMetric({commit},{id,name,description}){
              //  mutation editMetric 
              //        
                commit('editMetric',{id,name,description})
            },
               },      
        
    getters: {
        metrics(state) {
            //        vue
            return state.metrics
        }   
        
         }


}





